.text-title {
  font-weight: 700;
  font-size: 20px;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.content {
  border: 1px solid grey;
  display: inline-flex;
  font-family: sans-serif;
  justify-content: space-evenly;
  text-align: center;
  width: 100%;
}

.column-container {
  align-items: flex-end;
  border: 1px solid grey;
  height: 80vh;
  position: relative;
  width: 30%;
}

.center-bar {
  background-color: fuchsia;
  width: 4%;
  height: 100%;
  border: 1px solid grey;
  position: absolute;
  left: 48%;
  z-index: -1;
}

.tile {
  background-color: purple;
  border: 1px solid black;
  border-radius: 3px;
  height: 40px;
  margin: auto;
  z-index: 10;
}

.win-message {
  background-color: #080;
  border-radius: 10px;
  color: #ff0;
  font-size: 42px;
  font-weight: 700;
  left: 50%;
  position: absolute;
  text-align: center;
  text-shadow: 1px 1px #000;
  top: 40%;
  transform: translate(-50%, -50%);
  width: 40%;
  z-index: 20;
}

.win-subtitle {
  font-size: 24px;
  font-weight: 500;
  margin: 10px 0;
}

.win-number {
  color: #2ff;
}

/* New styles for input-container */
.input-container {
  margin-bottom: 20px;
  font-size: 20px;
  font-weight: 700; margin: 20px;
}

.error-message {
  color: red;
  margin-top: 5px;
}
